import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormGroupDirective } from '@angular/forms';
import { emailValidator } from 'src/app/theme/utils/app-validators';
import { ContactService } from './services/contact.service';
import { Contact } from './model/contact';
import { map, catchError } from 'rxjs/operators';
import { PropertyInsertSuccessAction, PropertyInsertErrorAction } from '../properties/store/property.actions';
import { of } from 'rxjs';
import { NotificationService } from 'src/app/services/notification.service';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  public contactForm: FormGroup;
  public lat = -0.307308;
  public lng = 36.0598205;
  public zoom = 14;
  @ViewChild(FormGroupDirective) formGroupDirective: FormGroupDirective;
  constructor(public formBuilder: FormBuilder, private contactService: ContactService, private notificationService: NotificationService) {

   }

  ngOnInit() {
    this.contactForm = this.formBuilder.group({
      name: ['', Validators.required],
      email: ['', Validators.compose([Validators.required, emailValidator])],
      phone: ['', Validators.required],
      message: ['', Validators.required]
    });
    const contact: Contact =  this.contactForm.value as Contact;
    this.contactService.createFeedback(contact);
    this.contactService.addToMailingList(contact.email);
  }

  public onContactFormSubmit(values: Object): void {
    if (this.contactForm.valid) {
      const contact: Contact = values as Contact;
      this.contactService.addToMailingList(contact.email);
      this.contactService.createFeedback(contact).subscribe(
        (val) => {
           this.notificationService.openSnackBar('Thank you email recieved we will get back to you within 24hrs', 'close');
           setTimeout(() =>
           this.formGroupDirective.resetForm(), 0);
        },
        response => {
          if (this.contactForm.valid){
            this.notificationService.openSnackBar('Error could not saved', 'close');
          }

        },
        () => {
            console.log('The PATCH observable is now completed.');
        });

    }
  }

}
